import { Component, Injectable } from '@angular/core';
import { SwPush } from '@angular/service-worker';
import { HttpClient } from '@angular/common/http';

@Injectable({ providedIn: 'root' })
export class NotificationService {
  constructor(private httpClient: HttpClient) {
  }

  doSubsciption(subsciption) {
    return this.httpClient.post('users/notification/subscribe', subsciption);
  }
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'frontend';

  readonly VAPID = {
    "publicKey": "BJAB_Tbjg6Nh49mlZUg7gmab8bmGCNQN4fYRdML8Y8-zyFvKRzztg7v6Xh75S2qwTwkr1IuUGTxXcXYNZbEPzqQ",
    "privateKey": "AzF9o313ItuMQulasxqXMJTtOKAYVvgIMqRhZDLwlRY"
  }

  constructor(
    private swPush: SwPush,
    private httpNotification: NotificationService
  ) {
    this.subscribeToNotifications()
  }

  subscribeToNotifications() {

    this.swPush.requestSubscription({
      serverPublicKey: this.VAPID.publicKey
    })
      .then(sub => {
        this.httpNotification.doSubsciption(sub)
          .subscribe(
            (res) => { console.log(sub); alert("subscribed") }
          )
      })
      .catch(err => console.error("Could not subscribe to notifications", err));
  }
}





